//Inicia en la pagina 1 
let pagina = 1;
const cita = {
    nombre: '',
    fecha: '',
    hora: '',
    servicios: []

}
document.addEventListener('DOMContentLoaded', function(){
    iniciarApp();
});
function iniciarApp(){
    mostrarServicios();
    //Resalta el DIV actual segun el Tab al que se presiona 
    mostrarSeccion();
    //Oculta o muesta una seccion seguna el tab al que se presiona
    cambiarSeccion();
    //Paginacion siguiente y anterior
    paginaSiguiente();

    paginaAnterior();
    //Comprueba la pagina actual para ocultar o mostrar la paginacion 
    botonesPaginador();

    //Muestra el resumen de la cita o mensaje de error si esta vacio
    mostrarResumen();
    //Almacena del nombre de la cita en el objeto
    nombreCita();
    //Almacena del fecha de la cita en el objeto
    fechaCita();
    //Deshabilitar fecha anterior 
    deshabilitarFechaAnterior();

    //Almacena la hora del cita en el objeto 
    horaCita();

}
function mostrarSeccion() {
    const seccionAnterior = document.querySelector('.mostrar-seccion');
    if (seccionAnterior) {
        seccionAnterior.classList.remove('mostrar-seccion');
    }
    const seccionActual = document.querySelector(`#paso-${pagina}`);
    seccionActual.classList.add('mostrar-seccion')
    const tabAnterior = document.querySelector('.tabs .actual');
    if (tabAnterior) {
        tabAnterior.classList.remove('actual');// Eliminar el Tabs anterior 
    }

    //Resalta el TABS actual 
    const tab = document.querySelector(`[data-paso="${pagina}"]`); //seleciona el tab actual 
    tab.classList.add('actual');
}

function cambiarSeccion() { // Para cambiar en cada una de las funciones 
    const enlaces = document.querySelectorAll('.tabs button')
    enlaces.forEach((enlace) =>{
        enlace.addEventListener('click', event => {
            event.preventDefault();
            pagina = parseInt((event.target.dataset.paso));

            //Eliminar mostrar seccion de la sesion anterior 
            document.querySelector('.mostrar-seccion').classList.remove('mostrar-seccion');

            // Eliminar el Tabs anterior 
            document.querySelector('.tabs .actual').classList.remove('actual');

            //volver a llamar la funcion de mostrar seccion 
            mostrarSeccion();
            botonesPaginador();
        })
    })
} 
function botonesPaginador(){
    
    const paginaSiguiente = document.querySelector('#siguiente')
    const paginaAnterior = document.querySelector('#anterior')
    if(pagina === 1) {
        paginaAnterior.classList.add('ocultar');
        paginaSiguiente.classList.remove('ocultar');
    }else if (pagina === 3) {
        paginaSiguiente.classList.add('ocultar')
        paginaAnterior.classList.remove('ocultar');
        mostrarResumen(); // ESTAMOS EN LA PAGINA 3 CARGA EL RESUUMEN DE LA CITA
    }else {
        paginaAnterior.classList.remove('ocultar');
        paginaSiguiente.classList.remove('ocultar');
    }
    mostrarSeccion()
}
function paginaSiguiente() {
    const paginaSiguiente = document.querySelector('#siguiente')
    paginaSiguiente.addEventListener('click', () => {
        pagina++;
        botonesPaginador();
    });
}
function paginaAnterior() {   
     const paginaAnterior = document.querySelector('#anterior')
     paginaAnterior.addEventListener('click', () => {
    pagina--;
    botonesPaginador();
});
}


async function mostrarServicios() { //La palabra “async” ante una función significa solamente una cosa: que la función siempre devolverá una promesa.
    try {
        const resultado = await fetch('./servicios.json'); // realizamos la consulta a la base de datos
        const db = await resultado.json(); //Le decimos el tipo de respuesta eque es, en este caso es "TIPO JSON"

        const {servicios} = db; // Permite desempacar valores de arreglos o propiedades de objetos en distintas variables."desestructuración", con las llaves se asigna automaticamente una variable
        //GENERAR EL HTML 
        servicios.forEach(servicio => {
            const {id, nombre, precio} = servicio;
            //DOM Scritting
            //Generar nombre de servicio
            const nombreServicio = document.createElement('P');
            nombreServicio.textContent = nombre;
            nombreServicio.classList.add('nombre-servicio')
            //Generar el precio del servicio
            const precioServicio = document.createElement('P');
            precioServicio.textContent = `₲ ${precio}`;
            precioServicio.classList.add('precio-servicio');
            //Generar DIV contenedor de servicios
            const servicioDiv = document.createElement('DIV');
            servicioDiv.classList.add('servicio');
            servicioDiv.dataset.idservicio = id;
            //Seleciona un servicio para la cita 
            servicioDiv.onclick = seleccionarServicio;
            //Inyectar precio y nombre al DIV de servicio
            servicioDiv.appendChild(nombreServicio);
            servicioDiv.appendChild(precioServicio);
            //Inyectarlo en el HTML a traves del ID servicios
            document.querySelector('#servicios').appendChild(servicioDiv);

        });
    } catch (error) {
        console.log(error);
    }
}
function seleccionarServicio(event) {
    let elemento;

    //Forzar el elemento al cual darle Click sea el DIV para poder realizar una seleccion del ID
    if(event.target.tagName === 'P') {
        elemento = event.target.parentElement;
        
    }else {
        elemento = event.target;
    }


    if(elemento.classList.contains('seleccionado')) {
        elemento.classList.remove('seleccionado');
        const id = parseInt(elemento.dataset.idservicio)
        eliminarServicio(id);

    }else {
        elemento.classList.add('seleccionado');
        const servicioObj = {
            id: parseInt(elemento.dataset.idservicio), //Seleccional el ID servivio
            nombre: elemento.firstElementChild.textContent,//Selecciona el primer elemento del DIV
            precio: elemento.firstElementChild.nextElementSibling.textContent // Selecciona el siquiente elemento hijo

        }
        agregarServicio(servicioObj);
    }
}
function eliminarServicio(idservicio){
    const {servicios } = cita;
    cita.servicios = servicios.filter(servicio => servicio.idservicio !== idservicio);
    console.log(cita)
}
function agregarServicio(servicioObj){
    const {servicios} = cita;
    cita.servicios = [...servicios, servicioObj]; //Los tres puntos es una forma de copiar un arreglo o objeto y asignarlo a otro arreglo
    console.log(cita)
}
function mostrarResumen (){
    //Destructuring 
    const {nombre, fecha, hora, servicios} = cita;
    //Seleccionar resumen
    const resumenDiv = document.querySelector('.contenido-resumen');
    
    //Limpia el HTML previo remplazandolo por un STRING vacio, la forma correcta de hacerlo es con un WHILE
    while(resumenDiv.firstChild){
        resumenDiv.removeChild(resumenDiv.firstChild);
    }
    //Forma de validar si un objeto esta vacio
    if (Object.values(cita).includes('')) {
        const noServicios = document.createElement('P');
        noServicios.textContent = "Falta datos de Servicios, Nombre, Fecha o Hora";
        noServicios.classList.add('invalidar-cita');
        //Agregar a resumen DIV
        resumenDiv.appendChild(noServicios);
    }else{

        const headingCita = document.createElement('H3');
        headingCita.textContent = 'Resumen de Cita';
        
        

        //Se extrae todo desde el objeto global cita
        const nombreCita = document.createElement('P');
        nombreCita.innerHTML = `<span>Nombre:</span> ${nombre}`;// A diferencia de textContent, innerHTML interprete como codigo HTML 

        const fechaCita = document.createElement('P');
        fechaCita.innerHTML = `<span>Fecha:</span> ${fecha}`; 

        const horaCita = document.createElement('P');
        horaCita.innerHTML = `<span>Hora:</span> ${hora}`;
        // Cra un div para el resumen contenedor servicio
        const servicioCita = document.createElement('DIV');
        servicioCita.classList.add('resumen-servicios');

        const headingServicio = document.createElement('H3');
        headingServicio.textContent = 'Resumen de Servicios';
        servicioCita.appendChild(headingServicio)
        let cantidad = 0;
        //Iterar sobre el arreglo de servicios
        servicios.forEach(servicio => {

            const { nombre, precio } = servicio;

            const contenedorServicio = document.createElement('DIV');
            contenedorServicio.classList.add('contenedor-servicio')
            const textServicio = document.createElement('P')
            textServicio.textContent = nombre;

            const precioServicio = document.createElement('P')
            precioServicio.textContent = precio;
            precioServicio.classList.add('precio')
            cantidad = cantidad + parseInt(precio.split('₲')[1])
            //Colocar texto y precio en el DIV
            contenedorServicio.appendChild(textServicio);
            contenedorServicio.appendChild(precioServicio);
            servicioCita.appendChild(contenedorServicio);
        });        
        resumenDiv.appendChild(headingCita)
        resumenDiv.appendChild(nombreCita);
        resumenDiv.appendChild(fechaCita);
        resumenDiv.appendChild(horaCita);
        resumenDiv.appendChild(servicioCita);
        const cantidadPagar = document.createElement('P');
        cantidadPagar.classList.add('total');
        cantidadPagar.innerHTML = `<span>Total a Pagar: </span> ₲ ${cantidad.toLocaleString('es-ES')}`;
        resumenDiv.appendChild(cantidadPagar);
        console.log(cantidadPagar)
        
    }
}
function nombreCita (){
    const nombreInput = document.querySelector('#nombre');
    nombreInput.addEventListener('input', e => {
        const nombreTexto = e.target.value.trim(); //Funcio trim para eliminar espacios en blanco del input
        //Validacion de Nombre texto no puede estar vacio
        if (nombreTexto === '' || nombreTexto.length <3){
            mostrarAlerta('Nombre no valido', 'error');
        }else {
            cita.nombre = nombreTexto;
            removerAlerta();
            // console.log(cita);
        }
    })
}
function mostrarAlerta(mensaje, tipo){
    //Si hay una alerta precio entonces no crear otra
    const alertaPrevia = document.querySelector('.alerta');
    if(alertaPrevia) {
        return; // Se podria usar en alertaPrevia.remove(); pero es preferible un return ya que este corta la ejecucion y para no generar mas HTML 
    }

    console.log('El mensaje es..',mensaje);
    const alerta = document.createElement('DIV');
    alerta.textContent = mensaje;
    alerta.classList.add('alerta');
    if (tipo === 'error') {
        alerta.classList.add('error')
        
        // console.log(alerta)
    }
    //Insertar en el HTML 
    const formulario = document.querySelector('.formulario');
    formulario.appendChild(alerta);
    //Eliminar Alerta despues de 3 segundos
    setTimeout(() => {
        removerAlerta();
    }, 3000);
    
}
// Remueve la alerta 
function removerAlerta() { 
    const removerAlerta = document.querySelector('.alerta')
    if (removerAlerta) {
        removerAlerta.remove();
    }
}
function fechaCita(){
    const fechaInput = document.querySelector('#fecha');
    fechaInput.addEventListener('input', e => {
        //getUTCDay permite retornar el numero del dia del 1 al 6
        const fecha = new Date(e.target.value).getUTCDay(); //Convertir un string en fecha
        
        if([0,6].includes(fecha)){ // Determina el dia seleccionado
            e.preventDefault();
            fechaInput.value = ''
            mostrarAlerta('Atencion dispnible solo entre semanas', 'error');
        }else {
            cita.fecha = fechaInput.value; //Agrega al objeto cita
        }
        // const opciones = {
        //     weekday: 'long',
        //     year: 'numeric',
        //     month: 'long',

        // }

        // console.log(fecha.toLocaleDateString('es-ES', opciones))

    })
    
}
function deshabilitarFechaAnterior(){
    const inputFecha = document.querySelector('#fecha');

    const fechaAhora = new Date();
    const year = fechaAhora.getFullYear();
    const month = fechaAhora.getMonth() + 1 ;
    const day = fechaAhora.getDay(); 
    //Formato deseado 2022-12-22
    const fechaDeshabilitada = `${year}-${month}-0${day}`;
    inputFecha.min = fechaDeshabilitada;
}
function horaCita(){
    const inputHora = document.querySelector('#hora');

    inputHora.addEventListener('input', e =>{
        const horaCita = e.target.value;

        const hora = horaCita.split(':'); //Permite identificar : dentro de un STRING y convertilo en lista
        //Ya convertido en lista podemos acceder al primer valor y condicionar por este valor
        if(hora[0] < 9 || hora[0] > 20){
            mostrarAlerta('HORA NO VALIDA', 'error');
            setTimeout(() => {
                inputHora.value = '';
            }, 2000);
            
        }else {
            cita.hora = horaCita;
            
        }
        

    })
}
